.login {
  border: 2px solid lightblue;
  width: 250px;
  border-radius: 8px;
  padding: 20px;
  margin: 0 auto;
  font-size: 25px;
  box-shadow: 6px 6px 5px 2px lightgrey;
}

.login-field {
    margin-bottom: 20px;

    .login-title {
      margin-top: 10px;
    }

    .login-input {
      width: 100%;
      font-size: 20px;
      margin-top: 10px;
      padding: 12px;
      border-radius: 10px;
      box-shadow: inset 4px 3px 2px lightgrey;
    }
  }

  .login-buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;

    .login-button {
      background-color: transparent;
      padding: 10px;
      border-radius: 25%;
      box-shadow: 2px 2px 0px 1px lightgrey;
      width: 40px;
      height: 40px;
    }

    .enter {
      color: rgb(125, 209, 125);
      border-color: rgb(125, 209, 125);
    }

    .exit {
      color: rgb(255, 147, 147);
      border-color: rgb(255, 147, 147);
    }

    .login-button:active {
      box-shadow: inset 2px 2px 0px 1px lightgrey;
    }
  }

.registration-message {
	display: flex;
	font-size: 15px;
	margin: 0 auto;
	margin-top: 20px;
	width: 270px;
	justify-content: space-between;
}

.registration-message-link{
	text-decoration: underline;
}

.registration{
	border: 2px solid lightblue;
	width: 400px;
	border-radius: 8px;
	padding: 20px;
	margin: 0 auto;
	font-size: 25px;
	margin: 0 auto;
	box-shadow: 6px 6px 5px 2px lightgrey;
}

.progress {
	margin-top: 5px;
	width: 100%;
	height: 10px;
	border-radius: 10px;
	background-color: lightgrey;
	margin: 10px 0 30px 0;
}

.progress-bar {
	transition: all 200ms ease-in;
}

.reg-options{
	display: flex;
	flex-direction: column;
}

.reg-checkbox {
	width: 20px;
	height: 20px;
	margin: 10px;
}